.form {
  width: 75%;
  margin: 0 auto;
}

.form__input {
  box-sizing: border-box;
  max-width: 100%;
  width: 100%;
  border: 1px solid #fff;
  background: transparent;
  color: #fff;
  padding: 1rem;
  margin-bottom: 1rem;
}

.form__input::placeholder {
  color: #e2e5e6;
}

.form__input:focus {
  outline-width: 0;
}

.form__information {
  font-size: 0.7rem;
  color: #fff;
}

.form__information a:visited {
  color: #fff;
}

.header {
  background: #101A4C;
}

.header h3 {
  text-align: right;
}

.header a {
  color: #fff;
  text-decoration: none;
}

.header a:visited {
  color: #fff;
}

.header--open {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.header__logo--open img {
  width: 3rem;
  display: block;
  margin: 0 auto;
}

.header__nav--open {
  align-items: center;
  justify-content: flex-end;
}

.header__overlay {
  padding: 0;
  height: 100%;
  width: 0%;
  position: fixed;
  background: #101a4c;
  z-index: 1;
  top: 0;
  left: 0;
  transition: 0.5s;
  overflow: hidden;
}

.header__overlay__item {
  text-align: center;
}

.header__overlay__item a {
  color: #fff;
  text-decoration: none;
}

.header__overlay__item a:visited {
  color: #fff;
}

.header__overlay__item a:hover {
  text-decoration: underline;
}

.header__overlay--close {
  text-align: right;
}

.header__overlay--close h1 {
  margin-right: 2rem;
}

.header--shadow {
  box-shadow: 0 20px 20px 0 rgba(0, 0, 0, 0.2);
}

.content {
  color: #fff;
  padding: 0.5rem;
}

.content--blue {
  background: #101a4c;
}

.content__card {
  padding: 1.5rem;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2);
}

.content--center {
  justify-content: center;
  text-align: center;
}

.page__header {
  margin-top: 0.5rem;
}

.page__header__content {
  background: linear-gradient(to right, #00f, #ed1c24);
}

.page__header__content h1 {
  margin: 0;
  padding: 0.5rem;
  padding-left: 1.5rem;
  color: #fff;
}

grid-overlay {
  position: absolute;
  left: 0;
  top: 0;
}

.btn {
  padding: 0.5rem 2rem;
  color: #fff;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 0.9rem;
  display: inline-block;
}

.btn__default {
  transition: all 0.2s;
  background: #fff;
  color: #101a4c;
  margin-top: 0.5rem;
  border: 1px solid #101a4c;
}

.btn__default:hover {
  background: #101a4c;
  color: #fff;
  border: 1px solid #fff;
}

.btn__default--invert {
  background: #101a4c;
  color: #fff;
  border: 1px solid #fff;
  font-size: 0.7rem;
}

.btn__default--invert:hover {
  background: #fff;
  color: #101a4c !important;
  border: 1px solid #101a4c;
}

.btn__social a {
  font-size: 1.4rem;
  text-align: center;
  transition: all 0.5s;
  margin-left: 1rem;
  background: #fff;
  color: #101a4c;
  width: 60px;
  height: 60px;
  line-height: 60px;
  display: block;
}

.btn__social a:hover {
  color: #fff;
  background: #101a4c;
  font-size: 1.5rem;
}

.btn__social a:visited {
  color: #101a4c;
  font-size: inherit;
}

.btn__social a:visited:hover {
  color: #fff;
  background: #101a4c;
  font-size: 2rem;
}

.btn__burger {
  text-align: right;
  margin-top: 0.5rem;
}

.btn--red {
  background: #B71C1C;
}

.btn--red:hover {
  border: 1px solid #B71C1C;
  color: #B71C1C;
}

.btn__tag {
  background: linear-gradient(to right, #00f, #ed1c24);
  border-radius: 10px;
  margin-right: 0.5rem;
  margin-top: 0.5rem;
}

.loader {
  text-align: center;
}

.loader svg path,
.loader svg rect {
  fill: red;
}

.project__card {
  align-content: space-between;
  height: 350px;
  padding: 0.5rem;
}

.project__card h4,
.project__card h2 {
  margin: 0;
  color: #fff;
}

.project-card__link {
  text-decoration: none;
}

.project-card__link h2:hover {
  text-decoration: underline;
}

.project__card--date span {
  background: #00bc00;
  background: -webkit-linear-gradient(left, #00bc00, #0ff);
  background: -o-linear-gradient(right, #00bc00, #0ff);
  background: -moz-linear-gradient(right, #00bc00, #0ff);
  background: linear-gradient(to right, #00bc00, #0ff);
  padding-left: 0.5rem;
  padding-right: 0.5rem;
}

.project__card--title h2 {
  background: #00f;
  background: -webkit-linear-gradient(left, #00f, #ed1c24);
  background: -o-linear-gradient(right, #00f, #ed1c24);
  background: -moz-linear-gradient(right, #00f, #ed1c24);
  background: linear-gradient(to right, #00f, #ed1c24);
  padding: 0.5rem 1rem;
}

.project-image {
  background: #e2e5e6;
  background-size: 102%;
  transition: all 0.2s ease-in-out;
}

.project-image:hover {
  cursor: pointer;
  background-size: 100%;
}

.project-image:hover h2 {
  text-decoration: underline;
}

.styleguide__color--1 {
  background-color: #fff;
}

.styleguide__color--2 {
  background-color: #000;
  color: #fff;
}

.styleguide__color--3 {
  background-color: #101a4c;
  color: #fff;
}

.styleguide__color--4 {
  background-color: #e2e5e6;
}

.styleguide__color--5 {
  background: linear-gradient(to right, #00f, #ed1c24);
  color: #fff;
}

.styleguide__color--6 {
  background: linear-gradient(to right, #00bc00, #0ff);
  color: #fff;
}

.styleguide__color h4,
.styleguide__color p {
  padding-left: 1rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
  margin: 0;
}

table {
  margin-top: 1.5rem;
  margin-bottom: 0.5rem;
  padding: 1rem 0;
  width: 100%;
  border-top: 1px solid #fff;
  border-bottom: 1px solid #fff;
}

table td {
  vertical-align: top;
}

table strong {
  text-transform: uppercase;
  padding-right: 1.5rem;
}

.social__container {
  display: flex;
  justify-content: flex-end;
}

.detail-image {
  width: 100%;
  height: 250px;
  object-fit: cover;
  transition: 0.3s;
}

.detail-image:hover {
  opacity: 0.8;
}

.popup {
  display: none;
  position: fixed;
  z-index: 1;
  padding-top: 100px;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: black;
  background-color: rgba(0, 0, 0, 0.9);
}

.popup__image {
  margin: auto;
  display: block;
  width: 80%;
  max-width: 700px;
  animation-name: zoom;
  animation-duration: 0.6s;
}

@keyframes zoom {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}

.popup__close {
  position: absolute;
  top: 15px;
  right: 35px;
  color: #f1f1f1;
  font-size: 40px;
  font-weight: bold;
  transition: 0.3s;
  cursor: pointer;
}

@media only screen and (max-width: 700px) {
  .popup__image {
    width: 100%;
  }
}

@media only screen and (max-width: 480px) {
  #authElement {
    display: none;
  }
}

.fb-grid__container__fw {
  max-width: 100%;
}
